.about{
    height: 100vh;
    margin-left: 5rem;
    display: flex;
    align-items: center;
}

.about-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}

.about-right{
    flex: 1;
}

.about-card{
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}
.about-card.bg{
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #333;
}
.about-image{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.about-award-img{
    width: 120px;
    height: 120px;
    border-radius: 20px;
}

.about-title{
    font-weight: 400;
}
.about-sub{
    margin: 20px 0px;
}
.about-desc{
    font-weight: 300;
}
.about-award{
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.about-award-texts{
    width: 70%;
}
.about-award-title{
    font-weight: bold;
    color: #555;
    margin-bottom: 10px;
}
@media screen and (max-width:480px){
    .about{
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
    } 
    .about-left{
        width: 100%;

    }
    .about-card{
        height: 30vh;
    }
    .about-card.bg,
     .about-award{
        display: none;
    }
    .about-right{
        padding: 20px;
    }
 }